/* src/styles/LoginScreen.css */
@import url('https://fonts.googleapis.com/css2?family=Gaegu:wght@400;700&display=swap');

.login-screen-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #a8e063, #56ab2f);
}

.login-box {
  text-align: center;
  padding: 40px 50px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 20px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  width: 90%;
  max-width: 450px;
  font-family: 'Gaegu', cursive;
}

.login-title {
  font-size: 3rem;
  font-weight: 700;
  color: #3a7bd5;
  margin-bottom: 10px;
  text-shadow: 1px 1px 2px rgba(0,0,0,0.1);
}

.login-subtitle {
  font-size: 1.2rem;
  color: #555;
  margin-bottom: 30px;
}

.login-form {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.input-group {
  position: relative;
  width: 100%;
}

.name-input {
  width: 100%;
  padding: 15px;
  font-size: 1.2rem;
  font-family: 'Gaegu', cursive;
  border: 2px solid #ddd;
  border-radius: 10px;
  transition: all 0.3s;
  text-align: center;
}

.name-input:focus {
  outline: none;
  border-color: #3a7bd5;
  box-shadow: 0 0 10px rgba(58, 123, 213, 0.3);
}

.char-count {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 0.9rem;
  color: #aaa;
}

.remember-me {
  display: flex;
  align-items: center;
  gap: 8px;
  color: #555;
  font-size: 1rem;
}

.error-message {
  color: #e74c3c;
  font-size: 1rem;
  font-weight: bold;
}

.start-button {
  width: 100%;
  padding: 15px;
  font-size: 1.5rem;
  font-family: 'Gaegu', cursive;
  font-weight: 700;
  color: #ffffff;
  background: linear-gradient(to right, #3a7bd5, #00d2ff);
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.start-button:hover:not(:disabled) {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.start-button:disabled {
  background: #bdc3c7;
  cursor: not-allowed;
}